import { genkit, defineFlow } from 'genkit';
import { googleAI } from '@genkit-ai/googleai';

console.log('[Oraculo IA] Configurando o ambiente Genkit (Configuração Padrão).');
console.log('[Oraculo IA] O plugin tentará encontrar a GOOGLE_API_KEY no ambiente.');

// Inicializa o Genkit com GoogleAI (Gemini) para backend
export const ai = genkit({
  plugins: [
    googleAI(),
  ],
  traceStore: {
    provider: 'dev', // Armazena traços localmente para depuração.
  },
  logLevel: 'debug', // Logs detalhados para desenvolvimento
});

// Flow de sugestão financeira
export const generateSavingSuggestions = defineFlow(
  {
    name: "generateSavingSuggestions",
    input: { question: "string", history: "any[]" },
    output: { answer: "string" },
  },
  async ({ question, history }) => {
    // Chamada direta ao Gemini via plugin googleAI
    const response = await ai.invoke("chat", {
      model: "gemini-pro",
      messages: [
        ...history,
        { role: "user", content: question }
      ]
    });
    return { answer: response.content ?? "" };
  }
);